import React from 'react';
import './Inovation.css';
import cross from '../../images/Inovation__cross.svg';
import kover from '../../images/Inovation__kovrik.svg';
import gearl from '../../images/Inovation__gearl.svg';

function Inovation() {  
  return (  
    <section className='inovation' id='inovation'>
      <div className='inovation__container-big'><p className='inovation__text-big'>   -         . 
                     ,   -     .   ?  :</p>  
      </div>
      <div className='inovation__container-m'>
        <p className='inovation__text'>          : , -, -, ,   ..</p>  
        <img className='inovation__tex-image-s2' src={kover} alt='image'/>
      </div>
      <div className='inovation__container-m'>
        <p className='inovation__text'>             ,  ,      - ,   ..</p> 
        <img className='inovation__tex-image-s1' src={cross} alt='image'/>
      </div>
      <div className='inovation__container-s'>
        <p className='inovation__text-s'>          </p>
      </div>
      <div className='inovation__container-s'>
        <p className='inovation__text-s'>,         </p>  
      </div>
      <div className='inovation__container-s'>
        <p className='inovation__text-s'> -    (,  ,  )</p>
      </div>  
      <div className='inovation__container-big'>
        <p className='inovation__text'>    
                
          (       ,  
              - )</p> 
          <img className='inovation__tex-image-big' src={gearl} alt='image'/>
      </div> 
    </section>
  );
};

export default Inovation;